.mb-3.modal-header{
  background-color: #ece8e8;
  margin-bottom: 0px !important;
}
.dark-layout .flex-grow-1.modal-body{
  background-color: #161D31;
}
.flex-grow-1.modal-body{
  background-color: #ffffff;
}
.dark-layout .mb-3.modal-header{
  background-color: #404656;
}


@import '../bootstrap-extended/include'; // Bootstrap includes
@import '../components/include'; // Components includes

.bordered-layout {

  
  .header-navbar {
    box-shadow: none;
    border: 1px solid $border-color;
    &.floating-nav {
      border: 1px solid $border-color;
    }
    &.fixed-top {
      border-bottom: 1px solid $border-color;
      background: $body-bg;
      box-shadow: none !important;
    }
  }
  .main-menu {
    border-right: 1px solid $border-color;
    box-shadow: none;
    &.menu-light .navigation > li {
      &.open:not(.menu-item-closing),
      &.sidebar-group-active {
        > a {
          background: darken($white, 7%);
        }
      }
    }
  }
  .dropdown-menu {
    border: 1px solid $border-color !important;
    box-shadow: none;
  }
  .main-menu .navigation,
  .main-menu {
    background: $body-bg;
  }
  .card,
  .bs-stepper:not(.wizard-modern):not(.checkout-tab-steps),
  .bs-stepper.wizard-modern .bs-stepper-content {
    border: 1px solid $border-color;
    box-shadow: none;
  }

  .footer {
    box-shadow: none !important;
  }
  .footer-fixed {
    .footer {
      border-top: 1px solid $border-color;
    }
  }
}
hr{
  border-top: 1px solid#E5D6B0;
}
.doBktq{
  border-top: 0.3px solid #E5D6B0;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  right: 5px;
}
input[type="date"]{
  text-align: start;
}